body {
  background-color: #14131a;
}

.Prow1 {
  padding: 0px 30px;
}

.Prow1 h2 {
  text-decoration: underline rgb(104, 154, 230) 2.5px;
  text-underline-offset: 10px;
  margin-bottom: 20px;
  font-weight: 500;
  color: white;
  text-align: left;
  font-size: 29px;
}

.project-container {
  padding: 25px;
}

.project-content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-around;
}

.project-card {
  flex: 1;
  background-color: #e9e8e8;
  border-radius: 10px;
  border: none;
  overflow-x: hidden;
  width: 23%;
  min-width: 250px;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  max-width: 450px;
  min-width: 290px;
}

.project-card img {
  width: 100%;
  max-height: 150px;

  object-fit: fill;
}

.card-body {
  padding: 15px;
}

.card-title {
  margin-top: 5px;
  font-weight: 500;
  font-size: 18px;
}

.card-text {
  font-weight: 400;
  text-align: left;
  line-height: 20px;
  font-size: 12px;
}

.btn {
  margin-top: 10px;
}
